import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "../../_assets/css/file.css"
import { baseURL, CONFIG, useStyles } from '../../configs/exports';
import Loader from "react-loader-spinner";
import "../../_assets/css/csv.css";
import * as moment from 'moment';
import {
    Card, CardBody, Table
} from 'reactstrap';
// import EditApplicant from './EditApplicant';
import { Autorenew } from "@material-ui/icons";
import clsx from "clsx";
import ReactDatatable from '@ashvin27/react-datatable';




const SMSLogs = () => {
    const [isLoading, setLoad] = useState(true)
    const [datatotal, setDataTotal] = useState()
    const [queryString, setQueryString] = useState("")
    const [smsLog, setSMSLog] = useState([])
    const [spin, setSpin] = useState(false);
    const refreshCanvas = () => {
        setSpin(true);
        setTimeout(() => {
            setSpin(false);
        }, 1000);
    };
    const classes = useStyles();



    // const [avatar, setAvatar] = useState("") // set profile picture


    useEffect(() => {
        // consuming API based on active tab
        let sms_url = baseURL + "sms?" + queryString;
        // setLoad(true)
        // tab 1 API call...
        axios.all([
            axios.get(sms_url, CONFIG),
        ]).then(axios.spread((smsResponse) => {
            let data = [];
            if (smsResponse.data.data.length > 0) {
                setLoad(false)
                for (let i = 0; i < smsResponse.data.data.length; i++) {

                    // format amount
                    let date = { dates: (moment.utc(smsResponse.data.data[i].created_on).format('DD/MM/YYYY HH:mm:ss')) };
                    data.push(Object.assign(date, smsResponse.data.data[i]));
                    setLoad(false)
                }
                setDataTotal(smsResponse.data.total)
                setSMSLog(data)
            } else {
                setLoad(false)
                setSMSLog(data)
            }
        })).catch(error => {
            // alert(JSON.stringify(error.response))
        })
    }, [queryString]);

    const config = {
        key_column: "tsc",
        length_menu: [100, 200, 500, 1000],
        show_filter: false,
        show_pagination: true,
        pagination: 'advance',
        page_size: 100,
        button: {
            csv: true
        },
        show_length_menu: true,
        language: {
            loading_text: "Please be patient while data loads...",
            filter: "Filter here..",
            no_data_text: "There was no record found",
            pagination: {
                next: <span>&#9658;</span>,
                previous: <span>&#9668;</span>
            }
        }
    }


    //sms columns list

    const columns_sms = [
        {
            key: "destination",
            TrOnlyClassName: 'cell', text: "Destination",
            className: "tsc",
            align: "left"
        },

        {
            key: "origin",
            TrOnlyClassName: 'cell', text: "Origin",
            className: "tsc",
            align: "left"
        },
        // {
        //     key: "status",
        //     TrOnlyClassName: 'cell', text: "Send",
        //     className: "tsc",
        //     align: "left"
        // },
        {
            key: "message",
            TrOnlyClassName: 'cell', text: "Message",
            className: "tsc",
            align: "left"
        },

        {
            key: "dates",
            TrOnlyClassName: 'cell', text: "Date Created",
            className: "tsc",
            align: "left"
        }
    ];




    // handles paginations
    const tableChangeHandler = data => {
        let queryString = Object.keys(data).map((key) => {
            if (key === "sort_order" && data[key]) {
                return encodeURIComponent("sort_order") + '=' + encodeURIComponent(data[key].order) + '&' + encodeURIComponent("sort_column")
                    + '=' + encodeURIComponent(data[key].column)
            } else {
                return encodeURIComponent(key) + '=' + encodeURIComponent(data[key])
            }
        }).join('&');
        setQueryString(queryString)
    }


    //UI returned
    return (
        <div style={{ marginTop: "-20px" }} >

            <Card>
                <CardBody>
                    <br />
                    <button className="float-right" title="refresh" style={{ background: "white", border: "0px" }}

                        onClick={
                            () => {
                                refreshCanvas()
                                setQueryString(require("randomstring").generate({
                                    length: 1,
                                    charset: 'alphanumeric',
                                    capitalization: 'lowercase'
                                }))
                            }}
                    >
                        Refresh
                        <Autorenew
                            className={clsx({
                                [classes.refresh]: true,
                                spin: spin
                            })}
                            onClick={
                                () => {
                                    refreshCanvas()
                                    setQueryString(require("randomstring").generate({
                                        length: 1,
                                        charset: 'alphanumeric',
                                        capitalization: 'lowercase'
                                    }))
                                }}
                            spin={360}
                        />

                    </button>

                    <div className="tabs tabs--bordered-top" >
                        <div className="tabs__wrap">

                            {isLoading === false ?

                                <Table>


                                    <div >
                                        Total SMS: <b>{datatotal}</b>
                                        <br /><br />
                                    </div>

                                    <ReactDatatable
                                        config={config}
                                        records={smsLog}
                                        columns={columns_sms}
                                        dynamic={true}
                                        id="tsc"
                                        loading={isLoading}
                                        total_record={datatotal}
                                        onChange={tableChangeHandler} />

                                </Table>

                                :
                                <div className="text-center h-50">
                                    <Loader
                                        type="Puff"
                                        color="#00BFFF"
                                        height={50}
                                        width={50} //3 secs
                                    />
                                </div>
                            }


                        </div>
                    </div>
                </CardBody>

            </Card>


        </div>
    );
}

export default SMSLogs;
